import React, { Component } from "react";
import Layout from "../components/Layout";
import Navbar from "../components/Navbar";

class Index extends Component {
  render() {
    return (
      <Layout pageTitle="Computicket Resales" pageStyles="">
        <Navbar />
        <h2>
          Reliably sell your tickets and purchase those available for resale
        </h2>
        <a className="btn btn-primary">Sell a ticket</a>
        <a className="btn btn-primary btn-outline-dark">Buy a ticket</a>
      </Layout>
    );
  }
}

export default Index;
